import React, { useEffect, useState } from "react";
import { useCart } from 'react-use-cart';
import { doc } from "firebase/firestore";
import ProductDataService from "../services/product.services";
import { Link } from "react-router-dom";

const ProductCart = ({ getProductId }, props) => {
    const [products, setProduct] = useState([]);
    useEffect(() => {
        getProducts();
    }, []);

    const getProducts = async () => {
        const data = await ProductDataService.getAllProducts();
        console.log(data.docs);
        setProduct(data.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
    };

    const {
        isEmpty,
        totalUniqueItems,
        items,
        totalItems,
        // cartTotal,
        updateItemQuantity,
        removeItem,
        // emptyCart,
    } = useCart();
    if (isEmpty) return <div className="container my-4 shadow p-3 mb-5 bg-body rounded">
        <div className="card mb-3">
            <div className="row g-0">
                <div className="col-md-4">
                    <img src="https://m.media-amazon.com/images/G/31/cart/empty/kettle-desaturated._CB424694257_.svg" className="img-fluid rounded-start" alt="..." />
                </div>
                <div className="col-md-8">
                    <div className="card-body">
                        <h4 className="card-title mt-4">Your PSP Basket is Empty</h4>
                        <Link to={"/"} className="card-text text-decoration-none" ><p>Shop today's deal</p></Link>
                        <button type="button" className="btn btn-warning mx-2">Sign in to your account</button>
                        <button type="button" className="btn btn-light">Sign up now</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
    return (
        <div>
            <div className="py-4 container">
                <div className="row justify-content-center">
                    <div className="col-12">
                        <h5>Cart ({totalUniqueItems}) total Items: ({totalItems}) </h5>
                        <table className="table table-light table-hover m-0">
                            <tbody>
                                {items.map((item, index) => {
                                    return (
                                        <tr key={index}>
                                            <td>
                                                <img src={item.image} style={{ height: '6 rem', maxWidth: "25%" }} alt="" />
                                            </td>
                                            <td>{item.title}</td>
                                            <td>{item.price}</td>
                                            <td>Quantity ({item.quantity}) </td>
                                            <td>
                                                <button
                                                    className="btn btn-info ms-2"
                                                    onClick={() => updateItemQuantity(item.id, item.quantity - 1)}
                                                >-</button>
                                                <button
                                                    className="btn btn-info ms-2"
                                                    onClick={() => updateItemQuantity(item.id, item.quantity + 1)}
                                                >+</button>
                                                <button className="btn btn-danger ms-2 my-2" onClick={() => removeItem(item.id)}>Remove Item</button>
                                            </td>
                                        </tr>
                                    )
                                })}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ProductCart;